import { Formik, Form, Field, FieldProps } from 'formik'
import { observer } from 'mobx-react-lite'
import React, { useEffect } from 'react'
import { Link } from 'react-router-dom'
import {Segment, Header, Comment, Loader} from 'semantic-ui-react'
import { useStore } from '../../../app/stores/store'
import * as Yup from 'yup';
import { formatDistanceToNow } from 'date-fns'

interface Props {
    eventityId: string;
}

const EventityDetailedChat = ({eventityId}: Props) => {
    const {commentStore} = useStore();

    useEffect(() => {
        if (eventityId) {
            commentStore.createHubconnection(eventityId);
        }
        return () => {
            commentStore.clearComments();
        }
    }, [commentStore, eventityId]);

    return (
        <>
            <Segment
                textAlign='center'
                attached='top'
                inverted
                color='teal'
                style={{border: 'none'}}
            >
                <Header>Chat about this event</Header>
            </Segment>
            <Segment attached clearing>
                    <Formik
                        onSubmit={(values, {resetForm}) =>
                            commentStore.addComment(values).then(() => resetForm())}
                        initialValues={{body: ''}}
                        validationSchema={Yup.object({
                            body: Yup.string().required()
                        })}
                    >
                        {({isSubmitting, isValid, handleSubmit}) => (
                            <Form className='ui form'>
                                <Field name='body'>
                                    {(props: FieldProps) => (
                                        <div style={{position: 'relative'}}>
                                            <Loader active={isSubmitting} />  
                                            <textarea
                                                placeholder='Type comment (ENTER to submit, SHIFT + ENTER for new lines)'
                                                rows={2}
                                                {...props.field}
                                                onKeyPress={e => {
                                                    if (e.key === 'Enter' && e.shiftKey) {
                                                        return;
                                                    }
                                                    if (e.key === 'Enter' && !e.shiftKey) {
                                                        e.preventDefault();
                                                        isValid && handleSubmit();
                                                    }
                                                }}
                                            />      
                                        </div>
                                    )}
                                </Field>
                            </Form>
                        )}
                    </Formik>

                <Comment.Group>
                    {commentStore.comments.map(comment => (
                        <Comment key={comment.id}>
                            <Comment.Avatar src={comment.image || '/assets/user.png'}/>
                            <Comment.Content>
                                <Comment.Author as={Link} to={`/profiles/${comment.username}`}>
                                    {comment.displayName}
                                </Comment.Author>
                                <Comment.Metadata>
                                    <div>{formatDistanceToNow(comment.createdAt)} ago</div>
                                </Comment.Metadata>
                                <Comment.Text style={{whiteSpace: 'pre-wrap'}}>{comment.body}</Comment.Text>
                            </Comment.Content>
                        </Comment>

                    ))}

                    

                </Comment.Group>
            </Segment>
        </>

    )
}

export default observer(EventityDetailedChat);
